let navPanel = document.querySelector(".vertical-nav");

let navItem = document.querySelectorAll(".vertical-nav__active");

let heightSection;
if (window.innerWidth > 1280) {
    heightSection = 1000;
} else if (window.innerWidth < 1280) {
    heightSection = 768;
}
function removeActive() {
    for (i = 0; i < navItem.length; i++) {
        if (navItem[i].classList.contains("active")) {
            navItem[i].classList.remove("active");
        }
    }
}

for (i = 0; i < navItem.length; i++) {
    navItem[i].addEventListener('click', function(e) {
        e.stopPropagation();
        removeActive();
    })
}

window.addEventListener('scroll', function(){
    if (window.scrollY >= 0 && window.scrollY < heightSection) {
        removeActive();
        navItem[0].classList.add("active");
    } else if ( window.scrollY < 2 * heightSection) {
        removeActive();
        navItem[1].classList.add("active");
    } else if ( window.scrollY < 3 * heightSection) {
        removeActive();
        navItem[2].classList.add("active");
    } else if (window.scrollY < 4 * heightSection) {
        removeActive();
        navItem[3].classList.add("active");
    } else if (window.scrollY < 5 * heightSection) {
        removeActive();
        navItem[4].classList.add("active");
    } else if (window.scrollY < 6 * heightSection) {
        removeActive();
        navItem[5].classList.add("active");
    } else if (window.scrollY < 7 * heightSection) {
        removeActive();
        navItem[6].classList.add("active");
    } else if (window.scrollY >= 7 * heightSection) {
        removeActive();
        navItem[7].classList.add("active");
    }
})

//slider-burgers
$(document).ready(function() {
    if ($(".slick-slider").length > 0 ) {
        $(".slick-slider").slick({
            arrows: true,
            dots: false,
            infinite: true,
            slidesToShow: 1,
            adaptiveHeight: true,
            prevArrow: "<img src='img/arrow-left.png' class='arrows'>",
            nextArrow: "<img src='img/arrow-right.png' class='arrows'>",
        });
    }
    //header-nav click
    $("#nav-list").on("click",".header__nav-item-link", function (event) {
        removeActive();
        event.preventDefault();
        $('body,html').animate({scrollTop: $($(this).attr('href')).offset().top}, 500);
        });
});
//menu-click
let menuItem = document.querySelectorAll('.menu__item');
    for (let i = 0; i < menuItem.length; i++) {
        menuItem[i].addEventListener('click', function(e) {
            e.preventDefault();
            for (let j = 0; j < menuItem.length; j++) {
                if (menuItem[j].classList.contains('active') && menuItem[j] !== menuItem[i]) {
                    menuItem[j].classList.toggle('active');
                }
            }
            menuItem[i].classList.toggle('active');
        })
}

//team-active
let teamList = document.querySelectorAll(".team__item");
for (let i = 0; i < teamList.length; i++) {
    teamList[i].addEventListener('click', function(e){
        e.preventDefault();
        teamList[i].classList.toggle("team-active");
    })
}        
//modal window quotes
function toggle (e){
    e.stopPropagation();
    const modal = document.querySelector(".quotes__modal-window");
  
    switch (modal.style.display){
      case 'flex':
        modal.style.display = "none";
        break
      case 'none':
        modal.style.display = "flex";
    }
}
document.querySelector(".quotes__modal-window").onclick = toggle;
document.querySelector(".quotes__close").addEventListener('click', toggle, true);
document.querySelector(".quotes__content").onclick = e => e.stopPropagation();
document.querySelector(".quotes").onclick = function(event){
    toggle(event);
}
//burgers__description-button-order
let buttonsBurgers = document.querySelectorAll(".burgers__description-button-order");
for (let i = 0; i < buttonsBurgers.length; i++) {
    buttonsBurgers[i].addEventListener('click', function(){
        removeActive();
    });
}
//footer__social-button
document.querySelector(".footer__social-button").addEventListener('click', function(){
    removeActive();
});
//submit-order click
let submitOrder = document.querySelector(".submit-order").addEventListener('click', function() {
    removeActive();
});

let buttonUp = document.querySelector(".fa-chevron-circle-up");
window.addEventListener('scroll', function() {
    if (window.scrollY > 500) {
        buttonUp.classList.add("active");
    }
    if (window.scrollY <= 500 && buttonUp.classList.contains("active")) {
        buttonUp.classList.remove("active");
    }
})